<template>
    <div class="OrderList">
        <Header>
            <i slot="first" class="iconfont icon-prev" @click="$router.go(-1)"></i>
            <p slot="mid">订单列表</p>
            <div slot="last"></div>
        </Header>
        <div class="list">
            <img src="../../images/img/aaa_07.png" alt="">
            <div class="xiao">
                <div class="shop">
                    <p>
                        <span>效果演示<i class="iconfont icon-arrow-backimg"></i></span>
                        <span>等待支付</span>
                    </p>
                    <p>
                        2019-09-08 18:39
                    </p>
                </div>
                <div class="shname">
                    <span>的味道切尔奇二群温</span><span>￥855.00</span>
                </div>
                <div class="money">
                    <span>去支付(还剩14分50秒)</span>
                </div>
            </div>
        </div>
    <Footer></Footer>
    </div>
</template>
<script>
import Header from "@/page/City/Header"
import Footer from "@/page/Footer/Footer"
export default {
    components:{
        Header,
        Footer
    },
   data() {
      return {
      }
   },
   created() {
   },
   methods: {
   }
}
</script>

<style lang="scss">
body{
    background: #F5F5F5;
}
.list{
    width: 100%;
    margin-top:0.85rem;
    background: white;
    display: flex;
    img{
        width: 1.2rem;
        height: 1.2rem;
        margin-left: 0.15rem;
    }
    .xiao{
        width:79%;
        font-size: 0.3rem;
        margin-top: 0.2rem;
        .shop{
            border-bottom: 2px solid rgb(247, 244, 244);
            p:nth-child(1){
                display: flex;
                line-height: 0.45rem;
                justify-content: space-between;
                span:nth-child(1){
                    font-size: 0.35rem;
                    i{
                        font-size: 0.3rem;
                        color: gray;
                    }
                }
            }
            p:nth-child(2){
                color: gray;
            }
        }
        .shname{
            border-bottom: 2px solid rgb(247, 244, 244);
            line-height: 0.8rem;
            display: flex;
            justify-content: space-between;
           
            span:nth-child(2){
                color: #ED640B;
            }
        }
        .money{
            line-height: 0.7rem;
            margin-left: 47%;
            span{
                width: 50%;
                border: 2px solid orange;
                border-radius: 0.08rem;
                color: orange;
            }
        }
    }
}

</style>


